@font-face {
    font-family: 'Raleway';
    src: local('Raleway'), url(/hearthstonedeck/static/media/Raleway-Black.eef1ff81.ttf) format('opentype');
  }

*, *::before, ::after {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    z-index: 9999;
}
nav {
    display: flex;
    position: relative;
    justify-content: space-around;
    align-items: center;
    min-height: 8vh;
    font-family: 'Raleway' sans-serif;
    background-color: rgb(102, 54, 157);
}

.logo {
    color: rgb(221, 217, 217);
    text-transform: uppercase;
    letter-spacing: 5px;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo-pic {
    height: 8vh;
}

.nav-links {
    display: flex;
    justify-content: space-around;
    width: 30%;
}

.nav-links li {
    list-style: none;
}

.nav-links a:hover {
    color:  rgb(230, 177, 177) ;
}

.nav-links a {
    color: rgb(221, 217, 217);
    text-decoration: none;
    letter-spacing: 3px;
    font-weight: bold;
    font-size: 14px;
}

.burger {
    display: none;
    cursor: pointer;
}

.burger div {
    width: 25px;
    height: 4px;
    background-color: rgb(221, 217, 217);
    border-radius: 10px;
    margin: 5px;
    transition: all 0.3s ease;
}

@media screen and (max-width: 1024px){
    .nav-links{
        width: 50%;
    }
}

@media screen and (max-width: 768px){
    body, html {
        overflow-x: hidden;
    }

    nav {
        transform: translateZ(10000px);
        z-index: 9999;
    }

    .logo {
        font-size: 1rem;
    }

    .nav-links {
        position: fixed;
        right: 0px;
        height: 92vh;
        top: 8vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: #5D4994;
        transform: translateX(100%);
        transition: transform 0.3s ease-in;
        z-index: 1000; 
    }

    .nav-links-toggle {
        position: absolute;
        right: 0px;
        height: 92vh;
        top: 8vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        background-color: #5D4994;
        width: 40%;
        transform: translateX(0%);
        transition: transform 0.3s ease-in;
        opacity: 0.9;
    }

    .nav-links-toggle a {
        color: rgb(221, 217, 217);
        text-decoration: none;
        letter-spacing: 3px;
        font-weight: bold;
        font-size: 1.15rem;
    }

    .nav-links li {
        opacity: 0;
    }

    .nav-links-toggle li {
        text-decoration: none;
        list-style: none;
        cursor: pointer;
    }

    .nav-links-toggle a:hover {
        color: rgb(230, 177, 177);
    }

    .burger {
        display: block;
    }
}

.nav-active {
    transform: translateX(0%);
}


.toggle .line1{
    transform: rotate(-45deg) translate(-5px,7px); 
}

.toggle .line2{
    opacity: 0;
}

.toggle .line3{
    transform: rotate(45deg) translate(-5px,-7px); 
}

